<template>
  <div class="home">
      <div class="headerp" :class="{'chang':headerScroll}">
          <van-icon class="ioc" :class="{cs:!headerScroll,changio:headerScroll}" name="wap-nav" />
          <div class="search">
              <span class="app-name">陈一天</span>
              <span class="sea">二手无限，尽情搜索</span>
          </div>
          <van-icon class="ioc" :class="{cs:!headerScroll,changio:headerScroll}" name="contact" />
      </div>
      <van-pull-refresh
      v-model="isLoading"
      success-text="刷新成功"
      @refresh="onRefresh"
      >
      <div ref="goods" class="goods">
          <div class="card"  v-for="(i,index) in goodsdata">
              <img :src="JSON.parse(i.images)[0]"/>
              <p>{{i.name}}</p>
              <p>{{i.price}}</p>
          </div>
      </div>
      <div v-if="loading" style="display: flex;justify-content: center;">
        <van-loading type="spinner" color="#1989fa" />
      </div>
      <div v-if="noMore">
        没有更多了
      </div>
      </van-pull-refresh>
  </div>
</template>

<script>
import { throttle ,debounce} from '../untli/debounce'
import { Toast } from 'vant';
  export default {
      data(){
          return{
              loading: false,
              page:1,
              headerScroll:false,
              isLoading: false,
              goodsdata:[],
              threshold: 1, // 距离底部多少像素时触发加载
              lenpage:2,

          }
      },
      computed:{
        noMore () {
          return this.page > this.lenpage && this.loading==false
        },
      },
      methods:{
        onRefresh() {
            setTimeout(() => {
                this.isLoading = false;
                Toast('刷新成功');
            }, 1000);
        },
        loadMore() {
        if(this.page<=this.lenpage){
        this.loading = true; // 开启 loading 状态
        // 模拟异步加载数据
        setTimeout(() => {
          this.$api.getgoods({page:this.page}).then(res=>{
            this.goodsdata.push(...(res.data.data));
            this.lenpage= Math.ceil(res.data.total/6);
          })
          this.page++;
          this.loading = false; // 关闭 loading 状态
        }, 2000);
      }else{
        this.loading=false;
        window.removeEventListener("scroll", throttle(this.handleScroll, 2000)); 
      }
        },
        handleScroll() {
            const scrollTop =document.documentElement.scrollTop || document.body.scrollTop; // 获取当前滚动位置
            const windowHeight = window.innerHeight; // 获取视口高度
            const scrollHeight =document.documentElement.scrollHeight || document.body.scrollHeight; // 获取文档总高度
            if (scrollHeight - (scrollTop + windowHeight) < this.threshold) {
            // 判断是否已滚动到底部
            this.loadMore(); // 触发加载更多
            }
        },
        pageScroll() {
            let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
            scrollTop > 100 ? this.headerScroll = true : this.headerScroll = false
        },
      },
      created(){
      },
      mounted(){
          window.addEventListener('scroll', this.pageScroll)
          this.loadMore(); // 首次加载数据
          window.addEventListener("scroll", throttle(this.handleScroll, 2000)); // 绑定滚动事件
      }
  }
</script>

