// 创建一个 Axios 实例
// const axios = axios.create({
//     baseURL: '/api',
//     timeout: 5000,
//   });
  
axios.interceptors.request.use(
    config => {
        //除登陆接口意外的接口都要加上token，才可以请求接口
        if (!config.url.endsWith('/api/jiauser')&&!config.url.endsWith('/api/clearn') &&!config.url.endsWith('/api/details') &&!config.url.endsWith('/api/goods') && !config.url.endsWith('/api/login') && !config.url.endsWith('/api/zhuc') && !config.url.endsWith('/api/type') && !config.url.endsWith('/api/yzzc') && !config.url.endsWith('/api/yz')) {
            // if(JSON.parse(sessionStorage.getItem('user')).token !=null){
            if(sessionStorage.getItem('user')){
            // if(localStorage.getItem('user')){
                // const token='Bearer '+JSON.parse(sessionStorage.getItem('user')).token
                const token=JSON.parse(sessionStorage.getItem('user')).token
                // const token=JSON.parse(localStorage.getItem('user')).token
                config.headers.common['Authorization']=token
            } 
        } 
        return config;
    },
    error => {
        return Promise.reject(error);
    }
);

