import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
const personal=()=>import('../views/Personal.vue')
const type=()=>import('../views/Type.vue')
const myshopping=()=>import('../views/MyShopping.vue')
const fabu=()=>import('../views/fabu.vue')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/type',
    name: 'type',
    component: type
  },
  {
    path: '/fabu',
    name: 'fabu',
    component: fabu
  },
  {
    path: '/myshopping',
    name: 'myshopping',
    component: myshopping
  },
  {
    path: '/personal',
    name: 'personal',
    component: personal
  },
  // {
  //   path: '/about',
  //   name: 'about',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: function () {
  //     return import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  //   }
  // }
]

const router = new VueRouter({
  routes
})

export default router
